<template>
  <q-card flat class="row" :class="`q-col-gutter-${gridGutter}`">
    <div v-if="canAdd" :class="gridClass">
      <q-card
        flat
        bordered
        class="card-quicklinks card-quicklinks-new border-add border-dashed bg-primary-1 full-height row items-center justify-center hover-border-secondary"
        @click="addBrand = true"
      >
        <q-card-section class="text-center flex-center flex">
          <q-avatar
            rounded
            size="55px"
            class="rounded q-pa-sm text-white bg-primary auto-hw"
          >
            <q-icon
              name="icon-plus"
              color="white"
              size="sm"
              class="q-pa-xs"
              style="color: white !important"
            />
          </q-avatar>
          <div class="text-body2 font-medium q-pt-md full-width">
            Add New Brand
          </div>
        </q-card-section>
      </q-card>
    </div>

    <!-- Brands List -->
    <transition
      appear
      :duration="500"
      enter-active-class="animated fade"
      leave-active-class="animated fade"
      v-for="(brand, i) in brands"
      :key="i"
    >
      <div :class="gridClass">
        <q-card
          flat
          bordered
          v-ripple:accent
          class="card-quicklinks brand full-height slide-hover row items-center hover-border-secondary"
          @click="$router.push('/brands/' + brand.id)"
        >
          <q-card-section class="self-start full-width q-px-lg q-pt-lg q-pb-md">
            <div
              v-if="brandCategories?.[brand?.brand_type]?.label"
              class="full-width"
            >
              <div class="row item-center q-gutter-sm justify-between">
                <q-badge
                  rounded
                  class="badge badge-md"
                  :class="{
                    'badge-bg-disable':
                      !brandCategories?.[brand?.brand_type]?.bg_color,
                    'badge-text-disable':
                      !brandCategories?.[brand?.brand_type]?.color,
                  }"
                  :style="`background-color: ${
                    brandCategories?.[brand?.brand_type]?.bg_color
                  }; color: ${brandCategories?.[brand?.brand_type]?.color}`"
                >
                  {{ brandCategories?.[brand?.brand_type]?.label }}
                </q-badge>
              </div>
              <q-icon
                flat
                round
                size="xs"
                name="icon-dots-horizontal"
                class="absolute-top-right all-pointer-events text-primary-500"
                style="top: 8px; right: 8px"
                @click.stop
                @click.prevent
              >
                <q-menu auto-close>
                  <q-list separator>
                    <q-item
                      clickable
                      class="hover-text-secondary"
                      @click.prevent
                    >
                      <q-item-section side class="q-pr-sm">
                        <q-icon name="icon-archive" size="xs" />
                      </q-item-section>
                      <q-item-section>Archive</q-item-section>
                    </q-item>
                    <q-item
                      clickable
                      class="text-negative hover-text-negative"
                      @click.prevent
                    >
                      <q-item-section side class="q-pr-sm">
                        <q-icon
                          name="icon-trash-01"
                          size="xs"
                          color="negative"
                        />
                      </q-item-section>
                      <q-item-section>Delete</q-item-section>
                    </q-item>
                  </q-list>
                </q-menu>
              </q-icon>
            </div>
            <div class="text-h5 font-medium q-pt-md q-pb-sm brand-title">
              {{ limitBrandName(brand?.brand_name) }}
            </div>
          </q-card-section>
          <q-card-actions
            align="right"
            class="full-width q-px-md bg-primary-1 self-end text-secondary text-body1 font-medium"
            :to="brand.id"
            :href="brand.id"
            style="height: 56px"
          >
            View Brand
            <q-icon
              size="20px"
              name="icon-arrow-narrow-right"
              class="q-pl-sm slide-hover-left-md"
            />
          </q-card-actions>
        </q-card>
      </div>
    </transition>

    <!-- Add New Brand -->
    <q-dialog
      persistent
      v-model="addBrand"
      transition-show="scale"
      transition-hide="scale"
      :transition-duration="300"
    >
      <CreateNewBrand
        style="max-width: 90vw; width: 640px"
        @finished="addBrand = false"
        @close="addBrand = false"
      />
    </q-dialog>
  </q-card>
</template>

<script>
import { defineComponent } from 'vue';
import CreateNewBrand from 'src/components/Brands/CreateNewBrand.vue';
import { mapState, mapActions } from 'pinia';
import useUser from 'stores/user';
import useGrowmodo from 'stores/growmodo';

export default defineComponent({
  name: 'BrandsList',

  components: { CreateNewBrand },

  props: {
    canAdd: {
      type: Boolean,
      default: true,
    },
    maxBrand: {
      type: Number,
      default: 0,
    },
    reverseList: {
      type: Boolean,
      default: true,
    },
    gridClass: {
      type: String,
      default: 'col-12 col-sm-6 col-md-3',
    },
    gridGutter: {
      type: String,
      default: 'md',
    },
  },

  data() {
    return {
      maxTitle: 33,
      addBrand: false,
    };
  },

  computed: {
    ...mapState(useUser, ['selectedOrgBrands']),
    ...mapState(useGrowmodo, ['brandCategories']),

    brands() {
      let visibleBrands = Object.values(this.selectedOrgBrands);
      // Check if Need to Reverse
      if (this.reverseList) visibleBrands = visibleBrands.reverse();
      // Slice to Maximimun Brands
      if (this.maxBrand && typeof this.maxBrand === 'number')
        visibleBrands = visibleBrands.slice(0, this.maxBrand);
      return visibleBrands;
    },
  },

  async mounted() {
    this.getOrgBrands();
  },

  methods: {
    ...mapActions(useUser, ['getOrgBrands']),

    limitBrandName(brandName) {
      let addDot = brandName?.length > this.maxTitle;
      return String(brandName).slice(0, 33) + (addDot ? '...' : '');
    },
  },
});
</script>
